import React, { Component } from 'react';
import './App.css';
import Fedbackcard from './Fedbackcard';
import img_elPic from './images/ProfileScreen_elPic_155628.png';
import btn_icon_937383 from './images/btn_icon_937383.png';
import btn_icon_929653 from './images/btn_icon_929653.png';
import btn_icon_back_profile from './images/btn_icon_back_profile.png';

// UI framework component imports
import Button from 'muicss/lib/react/button';
import Appbar from 'muicss/lib/react/appbar';


export default class ProfileScreen extends Component {

  // Properties used by this component:
  // appActions, deviceInfo

  constructor(props) {
    super(props);
    
    this.state = {
      textblock2: (<div>Graphic designer</div>),
      textblock2_plainText: "Graphic designer",
      textblock: (<div>John Doe</div>),
      textblock_plainText: "John Doe",
      textblock3: (<div>My Feedbacks</div>),
      textblock3_plainText: "My Feedbacks",
    };
  }

  onClick_elFab = (ev) => {
    // Go to screen 'Give a feedback!'
    this.props.appActions.goToScreen('giveafeedback', { transitionId: 'fadeIn' });
  
  }
  
  
  onClick_elIconButton = (ev) => {
    // Go to screen 'Menu'
    this.props.appActions.goToScreen('menu', { transitionId: 'slideIn_right' });
  
  }
  
  
  render() {
    // eslint-disable-next-line no-unused-vars
    let baseStyle = {};
    // eslint-disable-next-line no-unused-vars
    let layoutFlowStyle = {};
    if (this.props.transitionId && this.props.transitionId.length > 0 && this.props.atTopOfScreenStack && this.props.transitionForward) {
      baseStyle.animation = '0.25s ease-in-out '+this.props.transitionId;
    }
    if ( !this.props.atTopOfScreenStack) {
      layoutFlowStyle.height = '100vh';
      layoutFlowStyle.overflow = 'hidden';
    }
    
    const dataSheet_listData = this.props.dataSheets['listData'];
    const style_background = {
        width: '100%',
        height: '100%',
     };
    const style_background_outer = {
        backgroundColor: 'white',
        pointerEvents: 'none',
     };
    const style_rectangle = {
        background: 'rgba(73, 101, 255, 1.000)',
        pointerEvents: 'none',
     };
    const style_list = {
        height: 'auto',  // This element is in scroll flow
     };
    // Source items and any special components used for list/grid element 'list'.
    let items_list = [];
    let listComps_list = {};
    items_list = items_list.concat(this.props.appActions.getDataSheet('listData').items);
    
    const style_pic = {
        backgroundImage: 'url('+img_elPic+')',
        backgroundRepeat: 'no-repeat',
        backgroundPosition: '50% 50%',
        backgroundSize: 'cover',
        pointerEvents: 'none',
     };
    const style_fab = {
        display: 'block',
        textAlign: 'left',
        cursor: 'pointer',
     };
    const style_textBlock2 = {
        fontSize: 10.8,
        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", sans-serif',
        color: '#fefefe',
        textAlign: 'center',
        pointerEvents: 'none',
     };
    const style_textBlock = {
        fontSize: 15.1,
        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", sans-serif',
        fontWeight: 'bold',
        color: '#fefefe',
        textAlign: 'center',
        pointerEvents: 'none',
     };
    const style_textBlock3 = {
        fontSize: 15.1,
        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", sans-serif',
        fontWeight: 'bold',
        color: '#4a64ff',
        textAlign: 'center',
        pointerEvents: 'none',
     };
    const style_iconButton = {
        display: 'block',
        backgroundImage: 'url('+btn_icon_929653+')',
        backgroundRepeat: 'no-repeat',
        backgroundSize: '89.562%',
        backgroundPosition: '50% 0%',
        color: '(null)',
        textAlign: 'left',
        backgroundColor: 'transparent',
        textTransform: 'uppercase',
        cursor: 'pointer',
     };
    
    return (
      <div className="AppScreen ProfileScreen" style={baseStyle}>
        <div className="background">
          <div className='containerMinHeight elBackground' style={style_background_outer}>
            <div style={style_background} />
          
          </div>
          
          <div className='elRectangle' style={style_rectangle} />
        </div>
        <div className="layoutFlow" style={layoutFlowStyle}>
          <div className='hasNestedComps elList'>
            <div style={style_list}>
              {items_list.map((row, index) => {
                let itemClasses = `gridItem cols1_${index % 1} cols2_${index % 2} cols3_${index % 3}`;
                let itemComp = (row._componentId) ? listComps_list[row._componentId] : <Fedbackcard dataSheetId={'listData'} dataSheetRow={row} picture={row.picture} name={row.name} feedback={row.feedback} time={row.time} appActions={this.props.appActions} deviceInfo={this.props.deviceInfo} locStrings={this.props.locStrings} />;
                return (
                  <div className={itemClasses} key={row.key}>
                    {itemComp}
                  </div>
                )
              })}
            </div>
          
          </div>
          
        </div>
        <Appbar className="navBar">
          <div className="backBtn" onClick={ (ev)=>{ this.props.appActions.goBack() } }><img src={btn_icon_back_profile} alt="" style={{width: '50%'}} /></div>
        </Appbar>
        
        <div className="screenFgContainer">
          <div className="foreground">
            <div className='elPic' style={style_pic} />
            <Button className='actionFont elFab' style={style_fab}  variant="fab" color="primary" onClick={this.onClick_elFab} >
              <img src={btn_icon_937383} alt="" style={{width: '100%', marginTop: '4%'}} />
            </Button>
            <div className='elTextBlock2' style={style_textBlock2}>
              <div>{this.state.textblock2}</div>
            </div>
            <div className='elTextBlock' style={style_textBlock}>
              <div>{this.state.textblock}</div>
            </div>
            <div className='elTextBlock3' style={style_textBlock3}>
              <div>{this.state.textblock3}</div>
            </div>
            <button className='actionFont elIconButton' style={style_iconButton}  onClick={this.onClick_elIconButton}  />
          </div>
        </div>
      </div>
    )
  }
  

}



