import React, { Component } from 'react';
import './App.css';
import img_elLogo from './images/LoginScreen_elLogo.png';

// UI framework component imports
import Button from 'muicss/lib/react/button';


export default class LoginScreen extends Component {

  // Properties used by this component:
  // appActions, deviceInfo

  constructor(props) {
    super(props);
    
    this.state = {
      field: '',
      field2: '',
      textblock: (<div>Feedbacky</div>),
      textblock_plainText: "Feedbacky",
    };
  }

  textInputChanged_field = (event) => {
    this.setState({field: event.target.value});
  }
  
  textInputChanged_field2 = (event) => {
    this.setState({field2: event.target.value});
  }
  
  onClick_elButton = (ev) => {
    // Go to screen 'profile'
    this.props.appActions.goToScreen('profile', { transitionId: 'fadeIn' });
  
  }
  
  
  render() {
    // eslint-disable-next-line no-unused-vars
    let baseStyle = {};
    // eslint-disable-next-line no-unused-vars
    let layoutFlowStyle = {};
    if (this.props.transitionId && this.props.transitionId.length > 0 && this.props.atTopOfScreenStack && this.props.transitionForward) {
      baseStyle.animation = '0.25s ease-in-out '+this.props.transitionId;
    }
    if ( !this.props.atTopOfScreenStack) {
      layoutFlowStyle.height = '100vh';
      layoutFlowStyle.overflow = 'hidden';
    }
    
    const style_background = {
        width: '100%',
        height: '100%',
     };
    const style_background_outer = {
        backgroundColor: '#5465fe',
        pointerEvents: 'none',
     };
    const style_field = {
        display: 'block',
        backgroundColor: 'white',
        paddingLeft: '1rem',
        boxSizing: 'border-box', // ensures padding won't expand element's outer size
        color: 'rgba(0, 0, 0, 0.5000)',
     };
    const style_field2 = {
        display: 'block',
        backgroundColor: 'white',
        paddingLeft: '1rem',
        boxSizing: 'border-box', // ensures padding won't expand element's outer size
        color: 'rgba(0, 0, 0, 0.5000)',
     };
    const style_button = {
        display: 'block',
        textAlign: 'center',
        backgroundColor: '#fece00',
     };
    const style_button_outer = {
        cursor: 'pointer',
     };
    const style_logo = {
        backgroundImage: 'url('+img_elLogo+')',
        backgroundSize: '100% 100%',
        pointerEvents: 'none',
     };
    const style_textBlock = {
        fontSize: 28.0,
        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", sans-serif',
        fontWeight: 'bold',
        color: 'white',
        textAlign: 'center',
        pointerEvents: 'none',
     };
    
    return (
      <div className="AppScreen LoginScreen" style={baseStyle}>
        <div className="background">
          <div className='containerMinHeight elBackground' style={style_background_outer}>
            <div style={style_background} />
          
          </div>
          
        </div>
        <div className="layoutFlow" style={layoutFlowStyle}>
          <div className='baseFont elField'>
            <input style={style_field} type="email" placeholder="Username" onChange={this.textInputChanged_field} defaultValue={this.state.field}  />
          
          </div>
          
          <div className='baseFont elField2'>
            <input style={style_field2} type="password" placeholder="Password" onChange={this.textInputChanged_field2} defaultValue={this.state.field2}  />
          
          </div>
          
          <div className='actionFont elButton' style={style_button_outer}>
            <Button style={style_button}  onClick={this.onClick_elButton} >
              Sign In
            </Button>
          
          </div>
          
        </div>
        <div className="screenFgContainer">
          <div className="foreground">
            <div className='elLogo' style={style_logo} />
            <div className='elTextBlock' style={style_textBlock}>
              <div>{this.state.textblock}</div>
            </div>
          </div>
        </div>
      </div>
    )
  }
  

}



