import React, { Component } from 'react';
import './App.css';
import btn_icon_back_menu from './images/btn_icon_back_menu.png';

// UI framework component imports
import Appbar from 'muicss/lib/react/appbar';


export default class MenuScreen extends Component {

  // Properties used by this component:
  // appActions, deviceInfo

  render() {
    // eslint-disable-next-line no-unused-vars
    let baseStyle = {};
    // eslint-disable-next-line no-unused-vars
    let layoutFlowStyle = {};
    if (this.props.transitionId && this.props.transitionId.length > 0 && this.props.atTopOfScreenStack && this.props.transitionForward) {
      baseStyle.animation = '0.25s ease-in-out '+this.props.transitionId;
    }
    if ( !this.props.atTopOfScreenStack) {
      layoutFlowStyle.height = '100vh';
      layoutFlowStyle.overflow = 'hidden';
    }
    
    const style_background = {
        width: '100%',
        height: '100%',
     };
    const style_background_outer = {
        backgroundColor: 'white',
        pointerEvents: 'none',
     };
    const style_text = {
        color: '#4a64ff',
        textAlign: 'center',
     };
    const style_text_outer = {
        pointerEvents: 'none',
     };
    const style_text2 = {
        color: '#4a64ff',
        textAlign: 'center',
     };
    const style_text2_outer = {
        pointerEvents: 'none',
     };
    const style_text3 = {
        color: '#4a64ff',
        textAlign: 'center',
     };
    const style_text3_outer = {
        pointerEvents: 'none',
     };
    const style_text4 = {
        color: '#4a64ff',
        textAlign: 'center',
     };
    const style_text4_outer = {
        pointerEvents: 'none',
     };
    
    return (
      <div className="AppScreen MenuScreen" style={baseStyle}>
        <div className="background">
          <div className='elBackground' style={style_background_outer}>
            <div style={style_background} />
          
          </div>
          
        </div>
        <div className="layoutFlow" style={layoutFlowStyle}>
          <div className='baseFont elText' style={style_text_outer}>
            <div style={style_text}>
              <div>{this.props.locStrings.screen6_text_345041}</div>
            </div>
          
          </div>
          
          <div className='baseFont elText2' style={style_text2_outer}>
            <div style={style_text2}>
              <div>{this.props.locStrings.menu_text2_224668}</div>
            </div>
          
          </div>
          
          <div className='baseFont elText3' style={style_text3_outer}>
            <div style={style_text3}>
              <div>{this.props.locStrings.menu_text3_897585}</div>
            </div>
          
          </div>
          
          <div className='baseFont elText4' style={style_text4_outer}>
            <div style={style_text4}>
              <div>{this.props.locStrings.menu_text4_394953}</div>
            </div>
          
          </div>
          
        </div>
        <Appbar className="navBar">
          <div className="title">Menu</div>  <div className="backBtn" onClick={ (ev)=>{ this.props.appActions.goBack() } }><img src={btn_icon_back_menu} alt="" style={{width: '50%'}} /></div>
        </Appbar>
        
      </div>
    )
  }
  

}



