import React, { Component } from 'react';
import './App.css';
import img_elBitmap from './images/Fedbackcard_elBitmap_140931.jpg';


export default class Fedbackcard extends Component {

  // Properties used by this component:
  // picture, name, feedback, time

  constructor(props) {
    super(props);
    
    this.state = {
      textblock4: (<div>Johanna Doe</div>),
      textblock4_plainText: "Johanna Doe",
      textblock5: (<div>Hi John, the internal communication newsletter is a super useful idea!</div>),
      textblock5_plainText: "Hi John, the internal communication newsletter is a super useful idea!",
      textblock6: (<div>2 hours ago</div>),
      textblock6_plainText: "2 hours ago",
    };
  }

  render() {
    // eslint-disable-next-line no-unused-vars
    let baseStyle = {};
    // eslint-disable-next-line no-unused-vars
    let layoutFlowStyle = {};
    
    const style_rectangle2 = {
        background: 'rgba(239, 244, 247, 1.000)',
        boxShadow: '0.0px 2.0px 3px rgba(0, 0, 0, 0.3000)',
        pointerEvents: 'none',
     };
    const style_bitmap = {
        backgroundImage: 'url('+(this.props.picture || img_elBitmap)+')',
        backgroundRepeat: 'no-repeat',
        backgroundPosition: '50% 50%',
        backgroundSize: 'cover',
        pointerEvents: 'none',
     };
    const style_textBlock4 = {
        fontSize: 10.8,
        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", sans-serif',
        fontWeight: 'bold',
        color: 'black',
        textAlign: 'left',
        pointerEvents: 'none',
     };
    const value_textBlock4 = this.props.name;
    
    const style_textBlock5 = {
        fontSize: 10.8,
        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", sans-serif',
        color: 'rgba(0, 0, 0, 0.5000)',
        textAlign: 'left',
        pointerEvents: 'none',
     };
    const value_textBlock5 = this.props.feedback;
    
    const style_textBlock6 = {
        fontSize: 8.6,
        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", sans-serif',
        color: '#8e8e8e',
        textAlign: 'left',
        pointerEvents: 'none',
     };
    const value_textBlock6 = this.props.time;
    
    
    return (
      <div className="Fedbackcard" style={baseStyle}>
        <div className="compContent">
          <div className='elRectangle2' style={style_rectangle2} />
          <div className='elBitmap' style={style_bitmap} />
          <div className='elTextBlock4' style={style_textBlock4}>
            <div>{value_textBlock4 !== undefined ? value_textBlock4 : (<span className="propValueMissing">{this.state.textblock4}</span>)}</div>
          </div>
          <div className='elTextBlock5' style={style_textBlock5}>
            <div>{value_textBlock5 !== undefined ? value_textBlock5 : (<span className="propValueMissing">{this.state.textblock5}</span>)}</div>
          </div>
          <div className='elTextBlock6' style={style_textBlock6}>
            <div>{value_textBlock6 !== undefined ? value_textBlock6 : (<span className="propValueMissing">{this.state.textblock6}</span>)}</div>
          </div>
        </div>
      </div>
    )
  }
  

}



