import DataSheetBase from './DataSheetBase.js';
import datasheet_listData_row0_picture from './images/datasheet_listData_row0_picture.png';
import datasheet_listData_row1_picture from './images/datasheet_listData_row1_picture.png';
import datasheet_listData_row2_picture from './images/datasheet_listData_row2_picture.png';
import datasheet_listData_row3_picture from './images/datasheet_listData_row3_picture.png';
import datasheet_listData_row4_picture from './images/datasheet_listData_row4_picture.png';
import datasheet_listData_row5_picture from './images/datasheet_listData_row5_picture.png';

export default class DataSheet_listData extends DataSheetBase {

  constructor(id, updateCb) {
    super(id, updateCb);
    this.requestedKeyPath = "";  // this value can be specified in the React Studio data sheet UI
  }

  makeDefaultItems() {
    // eslint-disable-next-line no-unused-vars
    let key = 1;
    // eslint-disable-next-line no-unused-vars
    let item;
    
    item = {};
    this.items.push(item);
    item['picture'] = datasheet_listData_row0_picture;
    item['name'] = "Julia Carpenter";
    item['time'] = "2 days ago";
    item['feedback'] = "Create a feedback-rich culture at work. Keep your people happy, see trends";
    item.key = key++;
    
    item = {};
    this.items.push(item);
    item['picture'] = datasheet_listData_row1_picture;
    item['name'] = "Mate Rab";
    item['feedback'] = "Become more successful via continuous feedback.";
    item['time'] = "Now";
    item.key = key++;
    
    item = {};
    this.items.push(item);
    item['picture'] = datasheet_listData_row2_picture;
    item['name'] = "Madonna";
    item['feedback'] = "Clunky, slow processes are a no-no for the 21st century workforce.";
    item['time'] = "10 minutes ago";
    item.key = key++;
    
    item = {};
    this.items.push(item);
    item['picture'] = datasheet_listData_row3_picture;
    item['name'] = "Tibi Lakatos";
    item['feedback'] = "Get a quick glance at your direct reports’ performance.";
    item['time'] = "1 hours ago";
    item.key = key++;
    
    item = {};
    this.items.push(item);
    item['picture'] = datasheet_listData_row4_picture;
    item['name'] = "Peter Karakas";
    item['time'] = "2 hours ago";
    item['feedback'] = "Help mid-managers be more productive – conduct meaningful 1on1s, coach.";
    item.key = key++;
    
    item = {};
    this.items.push(item);
    item['picture'] = datasheet_listData_row5_picture;
    item['name'] = "Hipszter Janos";
    item['time'] = "4 days ago";
    item['feedback'] = "A feedback isn't worth much without follow-up. Plenuum assists and empowers everyone.";
    item.key = key++;
  }

}



